.new-expense__controls {
    display: flex;
    flex-wrap: wrap;
    gap: 1rem;
    margin-bottom: 1rem;
    text-align: left;
    color: rgb(247, 5, 5);
}

.new-expense__controls label {
    font-weight: bold;
    margin-bottom: 0.5rem;
    display: block;
}
.new-expense__controls input {
    font: inherit;
    padding: 0.5rem;
    border-radius: 6px;
    border: 1px solid rgb(0, 0, 0);
    width: 20rem;
    max-width: 100%;
}

.new-expense__actions {
     text-align: right;
}